#features {
    width: 100%;
    min-height: 100vh;
    margin: 3rem 0;
    background: url('../img/notebook-setup.jpg');
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    h2, h3 {
        text-align: center;
    }
}
.features-filter {
    width: 100%;
    min-height: 100vh;
    background-color: rgba($color: $space, $alpha: .7);
}
.features-section {
    width: 90%;
    margin: 0 auto;
    max-width: $max-width;
    @media only screen and (min-width: $cut) {
        width: 80%;
    }
}
.features-container {
    padding: 0 0 3rem;
}
.features-collection {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    @media only screen and (min-width: $cut) {
        display: grid;
        grid-template-columns: auto auto auto;
    }
}
.feature {
    max-width: 20rem;
    min-width: 10rem;
    display: flex;
    flex-direction: column;
    justify-content: start;
    padding: 2rem;
    background-color: rgba($color: $sec, $alpha: 0.6);
    border-top-right-radius: 0.5rem;
    border-top-left-radius: 0.5rem;
    border-bottom: 2px solid $sky;
    p {
        text-align: center;
        hyphens: auto;
    }
}
.feature-header {
    height: 3.5rem;
    h3 {
        font-size: 1.5rem;
        padding: 0;
        margin: 0;
    }
}
