.exception-container {
    display: flex;
    flex-direction: column;
    margin: 10px auto;
    max-width: 600px;
    text-align: center;
    h1 {
        font-size: 4em;
        color: $heat;
        line-height: 1;
        letter-spacing: -1px;
        margin: 30px 0;
    }
    span {
        font-size: 2rem;
    }
    .line {
        width: 80%;
        margin: 2rem auto;
    }
    .tip {
        margin-top: 5rem;
    }
  }