.fade-animation {
    -webkit-animation: fade 0.15s ease;
            animation: fade 0.15s ease;
}
@-webkit-keyframes fade {
    0% { opacity: 1; }
    100% { opacity: 0; }
}
@keyframes fade {
    0% { opacity: 1; }
    100% { opacity: 0; }
}
@-webkit-keyframes loading {
    from { -webkit-transform: rotate(0turn); transform: rotate(0turn); }
    to { -webkit-transform: rotate(1turn); transform: rotate(1turn); }
}
@keyframes loading {
    from { -webkit-transform: rotate(0turn); transform: rotate(0turn); }
    to { -webkit-transform: rotate(1turn); transform: rotate(1turn); }
}

@-webkit-keyframes pulse {
    from {
        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
        filter: alpha(opacity=40);
        opacity: .4;
        top: 0;
    }
    50% {
        -ms-filter: none;
        -webkit-filter: none;
        filter: none;
        opacity: 1;
        top: -10px;
    }
    to {
        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
        filter: alpha(opacity=40);
        opacity: .4;
        top: 0;
    }
}

@keyframes pulse {
    from {
        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
        filter: alpha(opacity=40);
        opacity: .4;
        top: 0
    }
    50% {
        -ms-filter: none;
        -webkit-filter: none;
        filter: none;
        opacity: 1;
        top: -10px
    }
    to {
        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
        filter: alpha(opacity=40);
        opacity: .4;
        top: 0
    }
}
