#projects {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  max-width: $max-width;
  padding-bottom: 5rem;
  margin: 0 auto;
  @media only screen and (min-width: $cut) {
    width: 80%;
  }
  h2 {
    padding: 0;
  }
  .line {
    width: 90%;
  }
}
.project-container {
  display: flex;
  flex-direction: column;
  padding: 0 3rem;
  border-radius: .5rem;
  box-shadow: inset 0 0 12px #000;
}
.project {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px auto;
  @media only screen and (min-width: $cut) {
    flex-direction: row;
    justify-content: space-between;
    margin: 20px auto;
  }
  img {
    max-width: 100%;
    height: auto;
    border-radius: 5px;
  }
  h3 {
    padding: 0;
    margin: 0;
    text-align: center;
    @media only screen and (min-width: $cut) {
      text-align: left;
    }
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 .5rem;
    margin: 1rem 0;
    @media only screen and (min-width: $cut) {
      justify-content: start;
      padding-left: 1.5rem;
    }
  }
  li {
    width: fit-content;
    padding: .25rem .5rem;
    margin: .25rem;
    color: $sec;
    font-size: .75rem;
    border: 1px solid $sec;
    border-radius: .25rem;
  }
  p {
    text-align: justify;
    hyphens: auto;
    height: auto;
    padding-right: 1.5rem;
    overflow-y: auto;
    @media only screen and (min-width: $cut) {
      height: 10rem;
    }
  }
}
.reverse-content {
  flex-direction: column;
  @media only screen and (min-width: $cut) {
    flex-direction: row-reverse;
  }
}
.image {
    width: 95%;
    display: flex;
    align-items: center;
    justify-content: center;
    @media only screen and (min-width: $cut) {
      width: 48%;
    }
}
.contents {
  @media only screen and (min-width: $cut) {
    width: 48%;
    padding: 1rem 0;
  }
}
.project-link {
  display: inline-block;
  margin: 10px 0px;
  padding: .25rem .5rem;
  font-weight: bold;
  background-color: transparent;
  border: 1px solid $link;
  border-radius: 10px;
  text-align: center;
  outline: none;
  cursor: pointer;
  transition: color 0.3s ease-out,
              background-color 0.3s ease-out,
              border-color 0.3s ease-out;
  &:hover {
    background-color: $link-hover;
    border-color: $link-hover;
    color: $ink;
    transition: color 0.3s ease-in,
                background-color 0.3s ease-in,
                border-color 0.3s ease-in;
  }
  &:active {
    background-color: $link-hover;
    border-color: $link-hover;
    color: $ink;
    transition: color 0.3s ease-in,
                background-color 0.3s ease-in,
                border-color 0.3s ease-in;
  }
}
