#about {
  min-height: 100vh;
  background-image: url("../img/desenvolvimento-web-mundo.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.about-filter {
  width: 100%;
  min-height: 100vh;
  background-color: rgba($color: $sec, $alpha: .3);
}

.container {
  width: 90%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  margin: 0 auto;
  max-width: $max-width;
  @media only screen and (min-width: $cut) {
    width: 80%;
  }
  .about-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba($color: $space, $alpha: 0.9);
    border-radius: .5rem;
    color: $sky;
    padding-top: 4rem;
    margin: 5rem 0;
    @media only screen and (min-width: $cut) {
      display: block;
    }
    h2 {
      font-weight: bolder;
      text-align: center;
      padding: 0 3rem;
      margin: 0 auto;
      @media only screen and (min-width: $cut) {
        text-align: left;
        padding: 0 5rem;
      }
    }
    p {
      font-size: 1.25rem;
      text-align: center;
      hyphens: auto;
      @media only screen and (min-width: 470px) {
        text-align: justify;
      }
    }
  }
}
.board-about {
  display: flex;
  justify-content: center;
  padding: 3rem;
  margin-top: 2rem;
  @media only screen and (min-width: $cut) {
    padding: 5rem;
  }
  .frame {
    width: 70%;
    height: fit-content;
    padding: 2rem;
    padding-top: 4rem;
    border: 4px solid $heat;
    border-radius: .5rem;
  }
}
.social-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  padding: .5rem 0;
  margin-top: 2rem;
  .icon {
    text-align: center;
    color: $link;
    width: 20px;
    height: 20px;
    font-size: 20px;
    padding: 10px;
    margin: 5px;
    border-radius: 50%;
    border: 2px solid $sec;
    transition: all .7s;
    &:hover {
      color: $link-hover;
      border-color: $link-hover;
    }
    &:active {
      color: $link-hover;
      border-color: $link-hover;
    }
    @media only screen and (min-width:$cut) {
      width: 35px;
      height: 35px;
      font-size: 35px;
    }
  }
}
.profile-pic-container {
  position: absolute;
  margin-top: -4rem;
  figure {
    margin: 0;
  }
}
.profile-pic {
  border: 4px solid $heat;
  border-radius: 50%;
}
