#soon {
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: bottom;
  width: 100%;
  min-height: 100vh;
}

.soon-header {
  width: max-content;
  z-index: 1;
  text-align: center;
  h1, span {
    user-select: none;
  }
  .soon-header-title {
    font-family: $logo;
    font-size: 3.5rem;
    display: inline-flex;
    line-height: 1;
    //background-image: $cold-gradient;
    //background-clip: text;
    //-webkit-background-clip: text;
    //-webkit-text-fill-color: transparent;
    @media only screen and (min-width:$cut){
      margin: 0;
      font-size: 4rem;
    }
  }
  .soon-header-subtitle {
    font-size: 2.25rem;
    display: block;
    line-height: 1;
    color: $heat;
    margin-top: 10px;
    @media only screen and (min-width:$cut){
      font-size: 3.5rem;
    }
  }
}
