#services {
    width: 90%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 5px auto;
    text-align: center;
    max-width: $max-width;
    @media only screen and (min-width: $cut) {
        width: 80%;
    }
    .container-icons {
        display: flex;
        justify-content: center;
    }
    .devicon-icons {
        width: 80%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        gap: 1.5rem;
        font-size: 4.5rem;
        padding: 0;
        @media only screen and (min-width: 1064px) {
            flex-wrap: nowrap;
        }
    }
}
.job {
    width: 20rem;
    margin: 1rem;
    padding: 1rem;
}
.jobs {
    text-align: center;
    font-size: 60px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    @media only screen and (min-width: 1064px) {
        flex-wrap: nowrap;
        font-size: 75px;
        flex-direction: row;
        align-items: start;
    }
    h3 {
        padding: 0;
    }
    p {
        font-size: 16px;
        padding: 0 1rem;
    }
    .divisor {
        width: 80%;
        height: 0;
        border-bottom: 2px solid $sec;
        border-left: none;
    }
    .v-line {
        display: none;
        @media only screen and (min-width: 1064px) {
            display: block;
            height: 8rem;
            border-left: 2px solid $sec;
            margin-top: 6rem;
        }
    }
    .line {
        display: block;
        width: 50%;
        margin: .25rem auto;
        @media only screen and (min-width: 1064px) {
            display: none;
        }
    }
}
