body {
    margin: 0;
    padding: 0;
    font-size: 100%;
    line-height: 1.5;
    color: $text;
    background-color: $space;
    font-family: $main-text;
    .line {
        color: $sec;
        border-color: $sec;
    }
    .preloader {
        background-color: $space;
        position: fixed;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        width: 100%;
        height: 100vh;
        z-index: 100;
        &::before {
            position: absolute;
            content: '';
            width: 5rem;
            height: 5rem;
            border-right: 1rem solid $space;
            border-top: 1rem solid $sky;
            border-left: 1rem solid $sky;
            border-bottom: 1rem solid $sky;
            border-radius: 50%;
            z-index: 101;
            -webkit-animation: loading 0.75s ease infinite .25s;
                    animation: loading 0.75s ease infinite .25s;
        }
        &::after {
            position: absolute;
            content: 'tt';
            font-size: 3rem;
            font-family: 'conthrax';
            color: $heat;
            z-index: 102;
        }
    }
}
article, aside, figcaption, figure, footer, header, nav, section {
    display: block;
}
h1, h2, h3, h4, stong {
    margin-top: 1em;
    padding-top: 1em;
    line-height: 1.25;
    color: $text;
}
h1 {
    font-size: 3rem;
}
h2 {
    font-size: 2.5rem;
}
h3 {
    font-size: 2rem;
}
h4 {
    font-size: 2.5rem;
}
ul, ol {
    list-style: none;
    margin: 1em 0;
    padding-left: 40px;
}
p, figure {
    margin: 1em 0;
}
a, :visited {
    text-decoration: none;
    color: $link;
    transition: color .3s;
    &:hover, :active {
    color: $link-hover;
    }
}
a img {
    border: none;
}
sup, sub {
    line-height: 0;
}
.unanimated {
    opacity: 0;
}