.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: $space;
  padding: 1rem;
  margin-top: 25px;
  text-align: center;
  color: $text;
  font-weight: bold;
  img {
    user-select: none;
    pointer-events: none;
  }
  .line {
    width: 30%;
    border-color: $sec;
  }
  .text-footer {
    margin: 0;
    font-weight: lighter;
  }
  .footer-content {
    max-width: 700px;
    margin: .5rem 0;
  }
}
.up {
  color: $link;
  position: fixed;
  bottom: 10rem;
  right: 1rem;
  display: block;
  text-align: center;
  font-size: 50px;
  cursor: pointer;
  &:hover {
      color: $link-hover;
  }
  &:active {
      color: $link-hover;
  }
  @media only screen and (min-width: $cut) {
      right: 3rem;
  }
}
.hide {
  display: none;
}