@font-face {
    font-family: 'conthrax';
    font-style: normal;
    font-weight: normal;
    src: 
        url('../fonts/conthrax/conthrax-sb.otf') format('otf'),
        url('../fonts/conthrax/conthrax-sb.ttf') format('ttf'),
        url('../fonts/conthrax/conthrax-sb.woff') format('woff'),
        url('../fonts/conthrax/conthrax-sb.woff2') format('woff2'); 
}

@font-face {
    font-family: 'rubik';
    font-style: normal;
    font-weight: normal;
    src: 
        url('../fonts/rubik/Rubik-Regular.otf') format('otf'),
        url('../fonts/rubik/Rubik-Regular.ttf') format('ttf'),
        url('../fonts/rubik/Rubik-Regular.woff') format('woff'),
        url('../fonts/rubik/Rubik-Regular.woff2') format('woff2'); 
}
