#particles-js {
  background: $space;
  display: flex;
  justify-content: center;
  vertical-align: bottom;
  width: 100%;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  .particles-js-canvas-el {
    width:100% !important;
    height:100vh !important;
  }
}

.header {
  z-index: 1;
  text-align: center;
  color: $text;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 0 auto;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  h1, span {
    user-select: none;
  }
  .header-title {
    font-family: $logo;
    font-size: 3.5rem;
    display: inline-flex;
    line-height: 1;
    background-image: $cold-gradient;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    @media only screen and (min-width:$cut){
      margin: 0;
      font-size: 5rem;
    }
  }
  .title-highlight { 
    font-family: $logo;
    font-size: 3.5rem;
    display: block;
    line-height: 1;
    background-image: $hot-gradient;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    @media only screen and (min-width:$cut){
      margin: 0;
      font-size: 5rem;
    }
  }
  .header-subtitle {
    font-size: 2.25rem;
    display: block;
    line-height: 1;
    color: $text;
    margin-top: 10px;
    @media only screen and (min-width:$cut){
      font-size: 3.5rem;
    }
  }
}

.header-links {
  margin: 10px;
  user-select: none;
  .link {
    font-size: 1.5rem;
    margin: 10px;
    @media only screen and (min-width:$cut){
      font-size: 2rem;
    }
  }
  a:first-child {
    animation-delay: .5s;
  }
  a:nth-child(2) {
    animation-delay: .75s;
  }
  a:last-child {
    animation-delay: 1s;
  }
}

.down {
  height: 5rem;
  color: $link;
  position: absolute;
  bottom: 0;
  margin: 0 auto;
  display: block;
  text-align: center;
  font-size: 30px;
  cursor: pointer;
  .icon {
      -webkit-transform-style: preserve-3d;
      transform-style: preserve-3d;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      width: 100px;
      height: 100px;
      fill: $link;
      -webkit-animation: pulse 1.3s infinite;
      animation: pulse 1.3s infinite;
      &:hover {
        color: $link-hover;
      }
      &:active {
        color: $link-hover;
      }
  }
}
